    background-repeat: no-repeat;
    background-size: cover;
    padding: 50px 0px;
    background-color: var(--primary-color);
}
.policy-landpage{
    background-color: var(--primary-color);
}
.buy-crypto-landpage{
    background-color: var(--primary-color);
}
.buy-bitcoin-text{
    width: 85%;
}
.about-page-land{
