}

.about-page-text{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.about-page-text h3{
    font-size: 2.5rem;
    color: white;
}
.about-page-text p{
    width: 75%;
}
.distorted-card-wrapper{
    width: 100%;
    height: 100%;
    background-color:var(--primary-color);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 30px 20px;
    cursor: pointer;
    transition:all 0.5s ease-out;
    clip-path: polygon(59% 0, 100% 51%, 100% 100%, 0 100%, 0 0);
    min-height: 300px;
}
.distorted-card-wrapper:hover{
    background-color: var(--primary-color);
    color: initial;
}
.distorted-card-wrapper h2{
    font-size: 1rem;
    text-transform: capitalize;
    align-self: flex-start;
    color: white;
    margin-bottom: 5px;
}
.distorted-card-wrapper h3{
    font-size: .9rem;
    align-self: flex-start;
    font-family: 'Montserrat';
    text-transform: capitalize;
    margin: 2px 0px;
    color: var(--secondary-color);
}
.distorted-line{
    align-self: flex-start;
    width: 80px;
    height: 2px;
    margin: 8px 0px;
}

.distort-container:hover span{
    color: var(--color-variant);
}
.distort-container:hover p{
    color:  var(--mild-color);
}
.distort-container{
    padding: 0px;
    background-color: var(--primary-color);
}

.distort-container:hover{
    border: 1px solid rgb(70, 123, 255);
}
.counter-distorted{
    top: 8px;
}
.counter-distorted svg{
    font-size: 2.5rem;
    color: var(--color-variant);
}
.widget-tab{
    display: flex;
    align-items: center;
    justify-content: space-around;
    align-self: flex-end;
    flex-direction: column;
}
.widget-container{
